@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header { 
    height: 150vh; 
    padding-top: 7rem;  
    overflow: hidden;
} 

.header_container { 
    text-align: center;  
    height: 100%;  
    position: relative; 
} 

.header_container > h5,h1 { 
    font-size: 1.2rem;
}

/*================ CTA ==============*/  
.cta { 
    margin-top: 2.5rem; 
    display: flex; 
    grid-gap: 1.2rem; 
    gap: 1.2rem; 
    justify-content: center; 
} 

/*================ Header Socials ==============*/ 
.header_socials { 
    display: flex; 
    flex-direction: column; 
    align-items: center; 
    font-size: 1.1rem; 
    grid-gap: 0.8rem; 
    gap: 0.8rem; 
    position: absolute; 
    left: 0; 
    bottom: 7rem; 
} 

.header_socials::after { 
    content: ''; 
    width: 1px; 
    height: 2rem; 
    background-color: var(--color-primary)
} 

/*================ me ==============*/ 
.me { 
    background: linear-gradient(var(--color-primary), transparent); 
    width: 22rem; 
    height: auto; 
    position: absolute; 
    left: calc(50% - 11rem); 
    margin-top: 4rem;  
    border-radius: 10rem 0rem 10rem 0rem; 
    overflow: hidden;  
} 
 
.me img { 
    width: 100%;  
    height: 100%;
}

/*============== Scroll Down ==============*/ 
.scroll_down { 
    position: absolute; 
    font-size: 1.1rem; 
    right: -2.3rem; 
    bottom: 10rem; 
    -webkit-transform: rotate(90deg); 
            transform: rotate(90deg); 
    font-weight: 200;
} 

/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) { 
    header { 
        height: 130vh;
    }
} 

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) { 
     
    .me{ 
        display: none;
    }

    header { 
        height: 100%;
    } 
     
    .header_socials, 
    .scroll_down { 
        display: none;
    }
}
nav { 
    background: rgba(0, 0, 0, 0.3); 
    width: -webkit-max-content; 
    width: max-content;  
    padding: 0.5rem 1.7rem; 
    z-index: 2; 
    position: fixed; 
    bottom: 2rem; 
    left: 50%; 
    -webkit-transform: translateX(-50%); 
            transform: translateX(-50%); 
    display: flex; 
    grid-gap: 1rem; 
    gap: 1rem; 
    border-radius: 3rem; 
    -webkit-backdrop-filter: blur(15px); 
            backdrop-filter: blur(15px);
} 

nav a { 
    background: transparent; 
    padding: 0.9rem; 
    border-radius:50%; 
    display: flex; 
    color: var(--color-light); 
    font-size: 1.1rem;
} 

nav a:hover { 
    background: rgba(0, 0, 0, 0.3);
} 

nav a.active { 
    background: var(--color-bg); 
    color: var(--color-white);
}
.about_container { 
    display: grid; 
    grid-template-columns: 30% 55%; 
    grid-gap: 15%; 
    gap: 15%;
} 

.about_me { 
    width: 100%;  
    aspect-ratio: 1/1;  
    border-radius: 2rem; 
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent); 
    display: grid; 
    place-items: center;
} 

.about_me_image { 
    border-radius: 2rem; 
    overflow: hidden; 
    -webkit-transform: rotate(10deg); 
            transform: rotate(10deg); 
    transition: var(--trasition);
} 

.about_me_image:hover { 
    -webkit-transform: rotate(0); 
            transform: rotate(0);
} 

.about_cards { 
    display: grid; 
    grid-template-columns: repeat(3, 1fr); 
    /* caso tenha que adicionar alguma coisa basta mudar o primeiro parâmetro do reapat*/ 
    grid-gap: 1.5rem; 
    gap: 1.5rem;
} 
 
.about_card { 
    background: var(--color-bg-variant); 
    border: 1px solid transparent; 
    border-radius: 1rem; 
    padding: 1.5rem; 
    text-align: center;
} 

.about_card:hover { 
    background: transparent; 
    border-color: var(--color-primary-variant); 
    cursor: default;
}
 
.about_icon { 
    color: var(--color-primary); 
    font-size: 1.7rem; 
    margin-bottom: 1rem;
} 

.about_card h5 { 
    font-size: 1.3rem;
} 

.about_card small { 
    font-size: 1rem; 
    color: var(--color-light); 
} 

.about_content p { 
    margin: 2rem 0 2.6rem; 
    color: var(--color-light); 
    font-size: 1rem; 
    text-align: justify;
} 

/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) {
    .about_container { 
        grid-template-columns: 1fr; 
        grid-gap: 0; 
        gap: 0;
    } 
     
    .about_me { 
        width: 70%; 
        margin: 2rem auto 4rem;
    } 
     
    .about_content p { 
        margin: 1rem 0 1.5rem;
    }
}

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) {
    .about_me { 
        width: 80%; 
        margin: 0 auto 3rem;
    } 

    .about_cards { 
        grid-template-columns: 1fr; 
        grid-gap: 1rem; 
        gap: 1rem;
    } 

    .about_content { 
        text-align: center;
    } 
    
    .about_content p { 
        margin: 1.5rem 0;
    }
}
.experience_container {   
    display: grid; 
    grid-template-columns: 1fr 1fr; 
    grid-gap: 2rem; 
    gap: 2rem;
} 

.experience_container > div { 
    background: var(--color-bg-variant); 
    padding: 2.4rem 5rem; 
    border-radius: 2rem; 
    border: 1px solid transparent; 
    transition: var(--trasition);
} 

.experience_container > div:hover { 
    background: transparent; 
    border-color: var(--color-primary-variant); 
    cursor: default;
} 

.experience_container > div h3 { 
    font-size: 1.5rem; 
    text-align: center; 
    margin-bottom: 2rem; 
    color: var(--color-primary);
} 

.experience_content { 
    font-size: 1rem; 
    display: grid; 
    grid-template-columns: 1fr 1fr; 
    grid-row-gap: 2rem; 
    row-gap: 2rem;
} 

.experience_details { 
    display: flex; 
    grid-gap: 1rem; 
    gap: 1rem;
} 

.experience_datails_icon { 
    margin-top: 6px; 
    color: var(--color-primary); 

} 

/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) {   

    .experience_container { 
        grid-template-columns: 1fr; 
    } 

    .experience_container > div { 
        width: 80%; 
        padding: 2rem; 
        margin: 0 auto;
    } 
} 

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) {
    .experience_container {  
        grid-gap: 1rem;  
        gap: 1rem; 
    } 

    .experience_container > div { 
        width: 100%; 
        padding: 2rem 1rem;
    }
}
.service_container { 
    display: grid; 
    grid-template-columns: repeat(3, 1fr); 
    grid-gap: 3rem; 
    gap: 3rem;
} 

.service { 
    background-color: var(--color-bg-variant); 
    border-radius: 0 0 2rem 2rem; 
    border: 1px solid var(--color-primary); 
    height: -webkit-fit-content; 
    height: -moz-fit-content; 
    height: fit-content; 
    transition: var(--trasition);
} 

.service:hover { 
    background: transparent; 
    border-color: var(--color-primary-variant); 
    cursor: default;
} 
 

.service_head { 
    background: var(--color-primary); 
    padding: 2rem; 
    border-radius:0 0 2rem 2rem; 
    box-shadow: 0 0.2rem 0.8rem #ff1616;
} 

.service_head h3 { 
    color: var(--color-bg); 
    font-size: 1.4rem; 
    text-align: center;
} 

.service_list { 
    padding: 2rem;
} 

.service_list li { 
    display: flex; 
    grid-gap: 1rem; 
    gap: 1rem; 
    margin-bottom: 0.8rem;
}

.service_list_icon { 
    font-size: 1.5rem; 
    color: var(--color-primary); 
    margin-top: 2px;
}  

.service_list p { 
    font-size: 1rem;
} 

/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) {
    .service_container { 
        grid-template-columns: 1fr 1fr; 
        grid-gap: 2rem; 
        gap: 2rem;
    } 
     
    .service { 
        height: auto;
    }
} 

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) {
    .service_container { 
        grid-template-columns: 1fr; 
        grid-gap: 1.5rem; 
        gap: 1.5rem;
    }
}


.portifolio_container { 
    display: grid; 
    grid-template-columns: repeat(3,1fr); 
    grid-gap: 2.5rem; 
    gap: 2.5rem;
} 

.portifolio_item { 
    background: var(--color-bg-variant); 
    padding: 1.3rem; 
    border-radius: 2rem; 
    border: 1px solid transparent;
    transition: var(--trasition);
} 

.portifolio_item:hover { 
    border-color: var(--color-primary-variant); 
    background: transparent;
} 

.portifolio_item_image { 
    border-radius: 1.5rem; 
    overflow: hidden;
} 

.portifolio_item h3 { 
    margin: 1.2rem 0 2rem;
} 

.portifolio_item_cta { 
    display: flex; 
    grid-gap: 1rem; 
    gap: 1rem; 
    margin-bottom: 1rem;
} 

/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) { 
    
    .portifolio_container { 
        grid-template-columns: 1fr; 
        grid-gap: 1.5rem; 
        gap: 1.5rem; 

    }
} 

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) { 


    .portifolio_container { 
        grid-template-columns: 1fr; 
        grid-gap: 1rem; 
        gap: 1rem; 
    }
}
.contact_container { 
    width: 58%; 
    display: grid; 
    grid-template-columns: 30% 58%; 
    grid-gap: 12%; 
    gap: 12%;
} 

.contact_options { 
    display: flex; 
    flex-direction: column; 
    grid-gap: 1.2rem; 
    gap: 1.2rem;
} 

.contact_option { 
    background: var(--color-bg-variant); 
    padding: 1.2rem; 
    border-radius: 1.2rem; 
    text-align: center; 
    border: 1px solid transparent; 
    transition: var(--trasition);
} 

.contact_option:hover { 
    background: transparent; 
    border-color: var(--color-primary-variant);
} 
 
.contact_option > h5, h4 { 
    font-size: 0.8rem;
}

.contact_option_icon { 
    font-size: 1.7rem; 
    margin-top: 0.5rem;
} 

.contact_option a { 
    margin-top: 0.7rem; 
    display: inline-block;
    font-size: 16px;
} 

/* ================ form =============*/ 
form { 
    display: flex; 
    flex-direction: column; 
    grid-gap: 1.2rem; 
    gap: 1.2rem;
} 

input, 
textarea { 
    width: 100%; 
    padding: 1.5rem; 
    border-radius: 0.5rem; 
    background: transparent; 
    border: 2px solid var(--color-primary-variant); 
    resize: none; 
    color: var(--color-white);
} 

/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) { 

    .contact_container { 
        grid-template-columns: 1fr; 
        grid-gap: 2rem; 
        gap: 2rem;
    }
} 

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) { 

    .contact_container { 
        width: var(--container-width-sm);
    }
}
footer {   
    display: flex; 
    justify-content: space-between;
    background-color: var(--color-primary); 
    padding: 1rem 0; 
    font-size: 0.9rem; 
    margin-top: 7rem;
} 

footer a { 
 color: var(--color-bg); 
 font-size: 20px;
}
 
.footer_socials { 
    padding: 2%; 
    display: flex;  
    grid-gap: 1rem;  
    gap: 1rem; 
    grid-column-gap: 1rem; 
    -webkit-column-gap: 1rem; 
            column-gap: 1rem; 
} 
 
.footer_copyright { 
    padding: 2%;
} 

/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) {
    footer { 
        flex-direction: column; 
        align-items: center; 
        padding: 8rem 0;
    }
} 

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) {
    footer { 
        margin-top: 10rem;

    }
}
* { 
    margin: 0; 
    padding: 0; 
    border: 0; 
    outline: 0; 
    list-style: none; 
    box-sizing: border-box; 
    text-decoration: none;
} 

:root { 
    --color-bg: #212121; 
    --color-bg-variant: #1c1c1c; 
    --color-primary: #ff1616; 
    --color-primary-variant: hsla(0, 100%, 54%, 0.473); 
    --color-white: #fff; 
    --color-light: rgba(255, 255, 255, 0.6); 
    
    --trasition: all 400ms ease;

    --container-width-lg: 75%; 
    --container-width-md: 86%; 
    --container-width-sm: 90%;
} 

html { 
    scroll-behavior: smooth; 
}  
 
::-webkit-scrollbar { 
    display: none;
} 

body {  
    font-family:monospace; 
    /*font-family: 'Poppins', sans-serif;*/ 
    background: #212121; 
    background: var(--color-bg); 
    color: #fff; 
    color: var(--color-white); 
    line-height: 1.7; 
    background-image: url(data:image/png;base64,);
} 

/* =================== GENEARL STYLES ============== */ 

.container { 
    width: 75%; 
    width: var(--container-width-lg); 
    margin: 0 auto;
} 

h1, 
h2, 
h3, 
h4, 
h5 { 
    font-weight: 500;
} 
 
h1 { 
    font-size: 3rem;
} 

section { 
    margin-top: 8rem; 
    height: 100%;
} 

section > h2,  
section > h5 { 
    font-size: 1.5rem; 
    text-align: center; 
    color: rgba(255, 255, 255, 0.6); 
    color: var(--color-light)
}
 
section > h2 { 
    color: #ff1616; 
    color: var(--color-primary); 
    margin-bottom: 3rem;
}
 
.text-light { 
    color: rgba(255, 255, 255, 0.6); 
    color: var(--color-light);
} 

a { 
    color: #ff1616; 
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--trasition)
} 

a:hover { 
    color: #fff; 
    color: var(--color-white);
} 

.btn { 
    width: -webkit-max-content; 
    width: max-content; 
    font-size: 1rem; 
    display: inline-block; 
    color: #ff1616; 
    color: var(--color-primary); 
    padding: 0.75rem 1.2rem; 
    border-radius: 0.4rem; 
    cursor: pointer; 
    border: 1px solid #ff1616; 
    border: 1px solid var(--color-primary); 
    transition: all 400ms ease; 
    transition: var(--trasition);
}
  
.btn:hover { 
    background: #fff; 
    background: var(--color-white); 
    color: #212121; 
    color: var(--color-bg); 
    border-color: transparent; 
} 

.btn-primary { 
    background: #ff1616; 
    background: var(--color-primary); 
    color: #212121; 
    color: var(--color-bg);
} 

img { 
    display: block; 
    width: 100%; 
    object-fit: cover;
}
 
/* =================== MEDIA QUERY TABLETS============== */ 
@media screen and (max-width: 1024px) {
    .container { 
        width: 86%; 
        width: var(--container-width-md);
    } 
    
    section { 
        margin-top: 6rem;
    }
} 

/* =================== MEDIA QUERY MOBILE ============== */ 
@media screen and (max-width: 600px) {
    .container { 
        width: 90%; 
        width: var(--container-width-sm);
    } 
    
    section > h2 { 
        margin-top: 2rem;
    }
}


